import { useEffect, useState } from 'react';
import { paramCase } from 'change-case';
import { useParams, useLocation } from 'react-router-dom';
// material
import { Container } from '@mui/material';
// redux
import { useDispatch, useSelector } from '../../redux/store';
import { getCompagnies } from '../../redux/slices/user';
// routes
import { PATH_DASHBOARD } from '../../routes/paths';
// hooks
import useSettings from '../../hooks/useSettings';
// components
import Page from '../../components/Page';
import HeaderBreadcrumbs from '../../components/HeaderBreadcrumbs';
import CompagniesNewForm from '../../components/_dashboard/compagnies/CompagniesNewForm';

// ----------------------------------------------------------------------

export default function AppPromoteCreate() {
  const { themeStretch } = useSettings();
  const dispatch = useDispatch();
  const { pathname } = useLocation();
  const { compagnieId } = useParams();
  const { compagnies } = useSelector((state) => state.user);
  const isEdit = pathname.includes('edit');
  const currentCompagnie = compagnies.find((superviso) => superviso?.id === Number(compagnieId));
  const [currentCompagnieLoaded, setCurrentCompagnieLoaded] = useState(false);
  useEffect(() => {
    if (currentCompagnie) {
      setCurrentCompagnieLoaded(true);
    }
  }, [currentCompagnie]);

  useEffect(() => {
    // Load data on the initial render
    dispatch(getCompagnies());
  }, []);

  return (
    <Page title="Compagnie: Créer un nouvel compagnie | RLP Center Lotto">
      <Container maxWidth={themeStretch ? false : 'lg'}>
        <HeaderBreadcrumbs
          heading={!isEdit ? 'Créer un nouvel compagnie' : 'Modifier compagnie'}
          links={[
            { name: 'Dashboard', href: PATH_DASHBOARD.root },
            { name: 'compagnie', href: PATH_DASHBOARD.company?.root },
            { name: !isEdit ? 'Nouvel Compagnie' : 'Modification' }
          ]}
        />

        {/* {(!isEdit || currentCompagnieLoaded) && (
          <CompagniesNewForm isEdit={isEdit} currentCompagnie={currentCompagnie} />
        )} */}
      </Container>
    </Page>
  );
}
